import { login, getUserInfo } from '@/api/jbr/user'
import { setToken, setUserName, setPass } from '../../common/js/catch'

// const Base64 = require('js-base64').Base64
const state = {
  userInfo: '',
  userName: 'Zee',
  isLogin: false,
  rules: {}
}
const getters = {
  // firstLetter: (state) => {
  //   return state.userName.substr(0, 1)
  // }
}
const mutations = {
  SET_USER_INFO(state, params) {
    state.userInfo = params
  },
  SET_IS_LOGIN(state, params) {
    state.isLogin = params
  },
  SET_USER_NAME(state, params) {
    state.userName = params
  },
  SET_RULES(state, rules) {
    state.rules = rules
  }
}
const actions = {
  /* 登录*/
  login({ commit }, { username, password }) {
    return new Promise((resolve, reject) => {
      login({ username, password }).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          setToken(res.data.token) // 保存token
          setUserName(username) // 保存登录名称
          setPass(password) // 保存密码
          commit('SET_IS_LOGIN', true)
          commit('SET_USER_INFO', res.data)
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  /* 登录*/
  UserInfo({ commit }, { id }) {
    return new Promise((resolve, reject) => {
      getUserInfo(id).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          // setToken(res.data.token) //保存token
          // setUserName(username) //保存登录名称
          // setPass(password) //保存密码
          // commit('SET_IS_LOGIN', true)
          // commit('SET_USER_INFO', response.data)
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  }
}

export default {
  getters,
  state,
  mutations,
  actions
}
