import Vue from 'vue'
import VeeValidate, { Validator } from 'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN'
// 配置中文
Validator.localize(zh_CN)

const config = {
  errorBagName: 'errors', // change if property conflicts.
  fieldsBagName: 'fieldBags', // 报冲突时 可自定义修改字段名称
  delay: 0, // 代表输入多少ms之后进行校验
  strict: true, // 没有设置规则的表单不进行校验，
  enableAutoClasses: false,
  locale: 'zh_CN', // 对语言（中文）的配置
  classNames: {
    touched: 'touched', // the control has been blurred
    untouched: 'untouched', // the control hasn't been blurred
    valid: 'valid', // model is valid
    invalid: 'invalid', // model is invalid
    pristine: 'pristine', // control has not been interacted with
    dirty: 'dirty' // control has been interacted with
  },
  events: 'input|blur', //* *input|blur** 在用户输入和表单失去焦点时都进行校验 可单独写  blur或input
  inject: true
}

// 自定义validate
const dictionary = {
  zh_CN: {
    messages: {
      TelOrMobile: () => '请输入正确的固定电话或手机号',
      email: () => '请输入正确的邮箱格式',
      phone: () => '请输入正确的手机号码',
      yhzhCheck: () => '请输入正确的银行账户',
      sbmCheck: () => '请输入正确的纳税人识别码',
      sfzLength: () => '请输入必须是18位身份证号',
      xydmLength: () => '请输入必须是18位社会信用代码',
      msgCode: () => '请输入必须是6位验证码',
      mmLength: () => '最少要输入 6 个字符',
      address: () => '请选择所在地区',
      pinLength: () => '请输入6位数字密码',
      fhsjCheck: () => '请选择发货时间',
      yjdhsj: () => '请选择预计到货时间',
      equal: () => '两次密码不一致',
      gzlCheck: () => '请输入正确工作量',
      required: (field) => field
    },
    attributes: {
      email: '请输入邮箱',
      password: '请输入密码',
      userAccount: '请输入账号',
      username: '请输入联系人姓名',
      phone: '请输入联系人手机',
      ztmc: '请输入公司全称',
      ztdm: '请输入统一社会信用代码',
      mm: '请输入登录密码',
      cfmm: '请输入重复密码',
      lxr: '请输入联系人',
      lxrdh: '请输入联系人手机号码',
      lxryx: '请输入联系人邮箱',
      ggyx: '请输入公共邮箱',
      yzm: '请输入验证码',
      sfzh: '请输入身份证号',
      yx: '请输入邮箱',
      sjhm: '请输入手机号码',
      zjmc: '请输入专家名称',
      smj: '请输入至少一个文件',
      PIN: '请输入移动证书Pin码',
      name: '请输入收货人姓名',
      sjh: '请输入收货人手机号',
      yb: '请输入邮编',
      jtdz: '请输入具体收货地址',
      address: '请输入所在地区',
      fptt: '请输入发票抬头',
      nsrsbm: '请输入纳税人识别码',
      fptt1: '请输入发票抬头',
      nsrsbm1: '请输入纳税人识别码',
      zcdd: '请输入注册地址',
      zcdh: '请输入注册电话',
      khyh: '请输入开户银行',
      yhzh: '请输入银行账户',
      sssy: '请输入送审事由',
      ssbz: '请输入送审备注',
      fhdmc: '请输入发货单名称',
      fhsj: '请选择发货时间',
      fhdd: '请输入发货地点',
      yjdhsj: '请选择预计到货时间',
      ydh: '请输入物流运单号',
      bcsl: '请输入本次发货数量',
      wtms: '请输入问题描述',
      sfybz: '请选择是否有包装',
      fwlx: '请选择服务类型',
      fjfs: '请选择返件方式',
      shdz: '请选择取货地址',
      fjdz: '请选择返件地址',
      xxdz: '请输入详细地址',
      khxm: '请输入客户姓名',
      tjsl: '请输入提交数量',
      gzl: '请输入正确工作量',
      rznr: '请输入日志内容',
      fl: '请选择分类',
      xm: '请选择项目'
    }
  }
}
Validator.localize(dictionary)

Vue.use(VeeValidate, config)

Validator.extend('yhzhCheck', {
  messages: {
    zh_CN: field => field + '请输入正确银行账户'
  },
  validate: value => {
    return /^([1-9]{1})(\d{14}|\d{18})$/.test(value)
  }
})

Validator.extend('sbmCheck', {
  messages: {
    zh_CN: field => field + '请输入正确纳税人识别码'
  },
  validate: value => {
    return /^[A-Za-z0-9]+$/.test(value)
  }
})

Validator.extend('gzlCheck', {
  messages: {
    zh_CN: field => field + '请输入正确工作量'
  },
  validate: value => {
    return value > 0 && value <= 7
  }
})

Validator.extend('phone', {
  messages: {
    zh_CN: field => field + '必须是11位手机号码'
  },
  validate: value => {
    return value.length === 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
})

Validator.extend('TelOrMobile', {
  messages: {
    zh_CN: field => field + '必须是11位手机号码'
  },
  validate: value => {
    return new RegExp(/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/).test(value) || new RegExp(/^1[3456789]\d{9}$/).test(value)
  }
})

// pin为6位
Validator.extend('pinLength', {
  messages: {
    zh_CN: field => field + '必须是6位数字'
  },
  validate: value => {
    return value.length === 6
  }
})

// 身份证为18位
Validator.extend('sfzLength', {
  messages: {
    zh_CN: field => field + '必须是18位身份证号'
  },
  validate: value => {
    return value.length === 18
  }
})
// 社会信用代码为18位
Validator.extend('xydmLength', {
  messages: {
    zh_CN: field => field + '必须是18位社会信用代码'
  },
  validate: value => {
    return value.length === 18
  }
})

// 密码长度最少为6位
Validator.extend('mmLength', {
  messages: {
    zh_CN: field => field + '最少是6位'
  },
  validate: value => {
    return value.length >= 6
  }
})
// 密码和重复密码是否一致
Validator.extend('equal', {
  messages: {
    zh_CN: field => '两次密码不一致'
  },
  validate: value => {
    return value === this.mm
  }
})
// 6位手机短信验证码
Validator.extend('msgCode', {
  getMessage: field => field + '为6位',
  validate: value => !!/^[a-zA-Z0-9]{6}$/.test(value)
})
// 昵称
Validator.extend('nickname', {
  getMessage: field => field + ' 只能包含字母、数字或中文',
  validate: value => !!/^[a-zA-Z0-9\u4e00-\u9fa5]+$/.test(value)
})
// 最大字符
Validator.extend('maxChar', {
  getMessage: (field, params) => {
    return field + '不能大于' + params[0] + '个字符.'
  },
  validate: (value, params) => {
    // eslint-disable-next-line no-control-regex
    return !(value.replace(/[^\x00-\xff]/g, 'xx').length > parseInt(params[0]))
  }
})
// 最小字符
Validator.extend('minChar', {
  getMessage: (field, params) => {
    return field + '不能小于' + params[0] + '个字符.'
  },
  validate: (value, params) => {
    // eslint-disable-next-line no-control-regex
    return !(value.replace(/[^\x00-\xff]/g, 'xx').length < parseInt(params[0]))
  }
})
Validator.extend('email', {
  messages: {
    zh: '请输入正确邮箱地址'
  },
  validate: value => {
    return /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/.test(value)
  }
})
Validator.extend('address', {
  messages: {
    zh: field => field + '请选择所在地区'
  },
  validate: value => {
    return value.length > 0
  }
})
