import { loadSearch, saveSearch, clearSearch } from '../../common/js/catch'
import { getWddbList } from '../../api/jbr/dbsx'
import { getWdxmList } from '../../api/jbr/wdxm'
// export const SET_SERACH_HISTOR = 'SET_SERACH_HISTOR' //搜索历史

// const Wdxm_jbr_LS_KEY = '2' //将半人我的项目历史
// const Dbsx_LS_KEY = '3' //代办事项历史
const state = {
  searchDbsxLists: loadSearch('Dbsx_LS_KEY'), // 代办事项历史
  searchjbrWdxmList: loadSearch('Wdxm_jbr_LS_KEY'), // 经办人我的项目历史
  searchList: []// 搜索历史
}

const getters = {

  // searchDbsxHistory: state => state.searchDbsxList,

  searchDbsxList: (state) => {
    return state.searchDbsxLists
  },

  searchjbrWdxmHistory: (state) => {
    return state.searchjbrWdxmList
  }
}
const mutations = {
  SET_DbsxSERACH_HISTOR(state, params) {
    state.searchDbsxLists = params
  },
  SET_WdxmSERACH_HISTOR(state, params) {
    state.searchjbrWdxmList = params
  }
}
const actions = {
  /**
   * 获取代办事项列表
   */
  // eslint-disable-next-line no-unused-vars
  getSEARListActions({ commit }, T) {
    return new Promise((resolve, reject) => {
      getWddbList(T)
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit('SET_USER_INFO', res.data)
            resolve(res.data)
          } else {
            reject(res.text)
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  /* 我的项目*/
  // eslint-disable-next-line no-unused-vars
  getjbrWdxmList({ commit }, T) {
    return new Promise((resolve, reject) => {
      getWdxmList(T).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          // setToken(res.data.token)
          if (T.xmzt === 2) {
            // commit('SET_WC_LIST', res.data)
          } else if (T.xmzt === 1) {
            // commit('SET_JXZ_LIST', res.data)
          } else {
            // commit('SET_ALL_LIST', res.data)
          }
          resolve(res.data)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 清空搜索历史
  clearJbrSearchHistory({ commit }, data) {
    if (data.key === '3') {
      commit('SET_DbsxSERACH_HISTOR', clearSearch(getType(data.key)))
    } else {
      commit('SET_WdxmSERACH_HISTOR', clearSearch(getType(data.key)))
    }
  },
  // 更新搜索历史
  updateJbrSearchList({ commit }, data) {
    if (data.key === '3') {
      commit('SET_DbsxSERACH_HISTOR', saveSearch(getType(data.key), data.query))
    } else {
      commit('SET_WdxmSERACH_HISTOR', saveSearch(getType(data.key), data.query))
    }
  },
  // 更新搜索历史
  // eslint-disable-next-line no-unused-vars
  deleteSearchHistory({ commit }, data) {
    // commit(SET_SERACH_HISTOR, saveSearch(data.query))
  }

  // const Wybm_LS_KEY = '0' //我要报名历史
  // const Wdxm_gys_LS_KEY = '1' //供应商我的项目历史
  // const Wdxm_jbr_LS_KEY = '2' //将半人我的项目历史
  // const Dbsx_LS_KEY = '3' //代办事项历史

}
function getType(key) {
  switch (key) {
    case '0':
      return 'Wybm_LS_KEY'
    case '1':

      return 'Wdxm_gys_LS_KEY'
    case '2':

      return 'Wdxm_jbr_LS_KEY'
    case '3':

      return 'Dbsx_LS_KEY'
  }
}
export default {
  getters,
  state,
  mutations,
  actions
}
