import request from '@/common/js/request'

export function getNoticesList(data) {
  return request({
    url: '/app/ananymous/ww/main/noteTicesList',
    method: 'post',
    encryption: true,
    data
  })
}
export function getNoticesAllList(data) {
  return request({
    url: '/app/ananymous/ww/main/noteTicesList',
    method: 'post',
    data
  })
}
