import axios from '../index'
import { getToken } from '../../common/js/catch'
// const BASE_URL = '/api'

/**
 * 登录接口
 */
export const login = ({ username, password }) => {
  return axios.request({
    url: '/app/login/applogin',
    method: 'post',
    token: false,
    data: {
      username,
      password
    }
  })
}

/**
 * 获取用户详情接口
 */
export const getUserInfo = (id) => {
  return axios.request({
    url: '/app/jbr/jbrxx?token=' + getToken(),
    method: 'post',
    token: false,
    data: {
      id
    }
  })
}

