import axios from '../index'
import { getToken } from '../../common/js/catch'
/**
 * 我的项目列表(pageno:分页, userid:用户id, query:搜索条件, xmzt:项目状态)
 */
export const getWdxmList = (data) => {
  return axios.request({
    url: 'api/xm/jbr/jbrxmlist?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
/**
 * 项目基本信息(id:项目id)
 * 返回值：Xm_zbxm_t
 */
export const getjbrXmjbxx = (data) => {
  return axios.request({
    url: 'api/xm/xm/getXmjbxxAndFbxx?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
/**
 * 项目分包信息(id:项目id)
 * 返回值：List<Xm_xmfb_t>
 */
export const getjbrXmfbxx = (data) => {
  return axios.request({
    url: 'api/xm/selectXmfbList?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
/**
 * 项目概述信息(id:项目id)
 * 返回值：List<Map<String,Object>>
 */
export const getjbrXmgsxx = (data) => {
  return axios.request({
    url: 'api/xm/getXmgsxx?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
