import { getjrjyxmlist, getxmsplist } from '../../api/gys/home'

// const Base64 = require('js-base64').Base64
const state = {
  message: ''
}
const getters = {}
const mutations = {
  SET_Message_INFO(state, params) {
    state.message = params
  }
}
const actions = {
  getxmsplist({ commit }, { page, userid }) {
    return new Promise((resolve, reject) => {
      getxmsplist({ page, userid })
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit('SET_USER_INFO', res.data)
            resolve(res.data)
          } else {
            reject(new Error('错误'))
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  },

  getjrjyxmlistAction({ commit }, { token, pageno, zzid, xmmc, jqdbsj }) {
    return new Promise((resolve, reject) => {
      getjrjyxmlist(token, pageno, zzid, xmmc, jqdbsj)
        .then(response => {
          // let response = JSON.parse(Base64.decode(res));//将json字符串转换成json对象
          if (response.state === 1) {
            // setToken(res.data.token)
            // commit('SET_USER_INFO', res.data)
            resolve(response.data)
          } else {
            reject(response.text)
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  }
}

export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions
}
