import axios from '../index'
import { getUrl } from '@/common/js/catch'
/**
 * CA登录接口
 */
export const caLogin = (data) => {
  return axios.request({
    url: '/app/ca/login/applogin',
    method: 'post',
    token: true,
    data
  })
}
/**
 * 移动证书下载
 */
export const CALoad = (data) => {
  return axios.request({
    baseURL: getUrl('VUE_APP_CFCA_API'),
    url: '/app/cfca/certDownload',
    method: 'post',
    token: true,
    data
  })
}
