import axios from '../index'
import { getToken } from '../../common/js/catch'
// import { getToken } from '../common/js/catch'
/**
 * 邀请函信息
 */
export const getGysbmXmYqh = (data) => {
  return axios.request({
    url: '/app/xm/gys/getGGxx?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
