<template>
  <div class="app">
    <keep-alive :include="keepAlive">
      <router-view v-if="isRouterAlive" />
    </keep-alive>
    <!--    <svg-icon></svg-icon>-->
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { setUrl } from '@/common/js/catch'
// import { getPage } from './common/js/catch' // 状态数据
// import svgIcon from './assets/svg'
// import xyNav from './components/nav/xy-nav.vue'
// import mixin from './store/plugin/mixin.js' // 混合被单独放在 mixin.js 中管理
// window.mixin = mixin // 将 混合/mixin 暴露在窗口对象中，某些组件需要时，直接提取 window.mixin
export default {
  provide() {
    return {
      reload: this.reload
    }
  },
  data() {
    return {
      isRouterAlive: true
      // keepAlive: 'main-keep-alive', //需要缓存的页面 例如首页
      // transitionName: 'slide-right', //初始过渡动画方向
      // enterAnimate: '', //页面进入动效
      // leaveAnimate: '' //页面离开动效
    }
  },
  computed: {
    ...mapState({
      pageLines: state => state.ui.pageLines,
      isLoading: state => state.router.isLoading,
      keepAlive: state => state.router.keepAlive

    })

  },

  watch: {
    // 监听 $route 为店内页设置不同的过渡效果
    '$route'(to) {
      const toDepth = to.path.split('/').length
      // const fromDepth = from.path.split('/').length
      // 同一级页面无需设置过渡效果
      // this.enterAnimate = toDepth > fromDepth ? 'animated fadeInRight' : 'animated fadeInLeft'
      // this.leaveAnimate = toDepth > fromDepth ? 'animated fadeOutLeft' : 'animated fadeOutRight'
      // 从店面页进入店内页 需要对店内页重新设置离开动效 因为他们处于不同 name 的 router-view
      if (toDepth === 3) {
        this.leaveAnimate = 'animated fadeOutRight'
      }
    }
  },
  created() {
    // var test = function() {
    //   // 回退到上一页面
    //   if (getPage() === true) {
    //     window.history.back()
    //   } else {
    //     window.history.back()
    //   }
    //   // this.$router.go(-1)
    // }
    // this.$nativeCore.registerJs('goBack', test)
    if (!window.localStorage.getItem('cyyList')) {
      window.localStorage.setItem('cyyList', JSON.stringify(['情况属实', '情况不属实', '审批通过']))
    }
    // 正式地址
    setUrl('VUE_APP_BASE_API', 'http://erp.xinyuan.com.cn:7003/xyerp')
    // 测试地址
    // setUrl('VUE_APP_BASE_API', 'http://193.193.193.35:9090/xyerp')
    // 本机
    // setUrl('VUE_APP_BASE_API', 'http://192.168.119.229:8087/xyerp')

    setUrl('VUE_APP_SC_BASE_API', 'http://erp.xinyuan.com.cn:7003/xyerp')
    setUrl('VUE_APP_API', 'http://erp.xinyuan.com.cn:7003/xyerp')
    setUrl('VUE_APP_FILE_API', 'http://erp.xinyuan.com.cn:7003/xyerp')
    setUrl('VUE_APP_CFCA_API', 'http://erp.xinyuan.com.cn:7003/xyerp')
    this.dissDialog()
    this.getRsaKey()
    window.localStorage.setItem('isFirst', JSON.stringify(1))
    this.getPhoneType() // 获取手机类型
  },
  mounted() {
    var self = this
    window['jpushAction'] = function(num) {
      console.info('jpushActionjpushAction=====')
      self.$router.push({
        name: 'DbsxIndex'
      })
    }
    this.$bridge.registerhandler('jpushAction', (data, responseCallback) => {
      console.info('registerhandler=====')
      self.$router.push({
        name: 'DbsxIndex'
      })
      responseCallback(data)
    })
    window['getRotuerPage'] = function(num) {
      console.info('keepAlive=====', self.$store.state.router.keepAlive)
      return self.$store.state.router.keepAlive
      // slef.imageData = num
    }
    window['goRotuerPage'] = function(num) {
      // console.info('keepAlive=====', self.$store.state.router.keepAlive)
      // return self.$store.state.router.keepAlive
      // slef.imageData = num
      console.info('goRotuerPage==1===', self.$store.state.router.keepAlive)
      console.info('goRotuerPage==2===', self.$store.state.router.keepAlive.length - 2)
      console.info('goRotuerPage==3===', self.$store.state.router.keepAlive[self.$store.state.router.keepAlive.length - 2])
      if (self.$store.state.router.keepAlive[self.$store.state.router.keepAlive.length - 2]) {
        self.$router.push({
          name: self.$store.state.router.keepAlive[self.$store.state.router.keepAlive.length - 2]
        })
      } else {
        self.$router.push({
          name: 'MhHome'
        })
      }
    }
    // this.backbutton()
    this.getJpushPage()
  },

  methods: {
    getPhoneType() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          var phoneType = window.myapp.getPhoneType()
          if (phoneType === 'android') {
            console.info('phoneType==111======')
            import('@/assets/style/android.less')
            console.info('phoneType==222======')
          }
        } catch (err) {
          console.log(err)
        }
      } else {
        // this.$toast('返回原生页面')
      }
    },
    reload() {
      this.isRouterAlive = false
      this.$nextTick(function() {
        this.isRouterAlive = true
      })
    },
    getJpushPage() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          console.info('gopage==111======')
          var gopage = window.myapp.getJpushPage()
          console.info('gopage====222====', gopage)
          if (gopage) {
            this.$router.push({
              name: gopage
            })
          }
        } catch (err) {
          console.log(err)
        }
        // try {
        //   window.webkit.messageHandlers.dissDialog.postMessage()
        // } catch (err) {
        //   console.log(err)
        // }
      } else {
        // this.$toast('返回原生页面')
      }
    },
    // 获取rsa加密公钥
    getRsaKey() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          const jmKey = window.myapp.getRsaKey()
          this.$store.commit('mh/SET_JMKEY', jmKey)
        } catch (err) {
          console.log(err)
        }
        try {
          this.$bridge.callhandler('getRsaKey', '', (jmKey) => {
            console.log('公钥jmKey=============================', jmKey)
            this.$store.commit('mh/SET_JMKEY', jmKey)
          })
        } catch (err) {
          console.log(err)
        }
      } else {
        // this.$toast('返回原生页面')
      }
    },
    dissDialog() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.dissDialog()
        } catch (err) {
          console.log(err)
        }
        try {
          window.webkit.messageHandlers.dissDialog.postMessage()
        } catch (err) {
          console.log(err)
        }
      } else {
        // this.$toast('返回原生页面')
      }
    }
  }
}
</script>
<style lang="less">
  @import "./assets/style/login.less";
  @import "./assets/style/master.less";
  @import "./assets/style/mall.less"; // 商城样式
  @import "./assets/style/bid.less"; // 竞价
  @import "./assets/style/erp.less"; // 信源ERP
  // @import "./assets/style/android.less"; // 适配安卓机型
</style>

