
const state = {
  isLoading: false,
  pageParams: {},
  pageList: [], // 处理页面返回
  searchPath: '',
  keepAlive: [],
  footMeun: [], // 底部下边栏
  footNum: 0, // 当前下边栏页面
  list: [], // 路由文件
  msgCount: ''
}
const getters = {
  getPageList: (state) => {
    return state.pageList
  }
}
const mutations = {

  SET_MsgCount: (state, msgCount) => {
    state.msgCount = msgCount
  },

  SET_Foot_Num: (state, footMeun) => {
    state.footNum = footMeun
  },

  SET_Foot_Meun: (state, footMeun) => {
    state.footMeun = footMeun
  },
  SET_KEEP_ALIVE: (state, keepAlive) => {
    state.keepAlive = keepAlive
  },
  setPageList(state, params) {
    state.pageList = params
  },
  updatePageParams(state, params) {
    state.pageParams = params
  },
  updateLoadingStatus(state, isLoading) {
    state.isLoading = isLoading
  },

  SET_SEARCH_PATH(state, params) {
    state.searchPath = params
  },
  add(state, name) {
    state.list.indexOf(name) < 0 && state.list.push(name)
  },
  remove(state, name) {
    state.list = state.list.filter(v => {
      return v !== name
    })
  },
  clean(state) {
    state.list = []
  }
}
const actions = {
  setPageParams({ commit }, data) {
    commit('updatePageParams', data)
  },
  setKeepAlive({ commit }, data) {
    commit('SET_KEEP_ALIVE', data)
  },
  addKeepAlive({ commit, state }, data) {
    var list = []
    list = state.keepAlive
    console.log('data========', data)
    console.log('state========', state)
    console.log('list========', list)

    const index = list.indexOf(data)
    if (index === -1) {
      list.push(data)
    }
    console.log('addKeepAlive========', list)
    commit('SET_KEEP_ALIVE', list)
  },
  // 删除当前页
  remKeepAlive({ commit, state }, data) {
    var list = []
    list = state.keepAlive
    const index = list.indexOf(data)
    if (index !== -1) {
      list.splice(index, 1)
    }
    // this.delitem(list, data)
    console.log('remPageList========', list)
    commit('SET_KEEP_ALIVE', list)
  },
  // 增加当前页
  addPageList({ commit, state }, data) {
    var list = []
    list = state.pageList
    const index = list.indexOf(data)
    if (index === -1) {
      list.push(data)
    }
    console.log('beforeRouteEnter========', list)
    commit('setPageList', list)
  },
  // 删除当前页
  dlePageList({ commit, state }, data) {
    var list = []
    list = state.pageList
    const index = list.indexOf(data)
    if (index !== -1) {
      list.splice(index, 1)
    }
    // this.delitem(list, data)
    commit('setPageList', list)
  },
  // 请空跳转页
  cleanPageList({ commit, state }, data) {
    var list = []
    commit('setPageList', list)
  }
}

export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions
}
