// import Cookies from 'js-cookie'
import storage from 'good-storage'
const SEARCH_KEY = '__search__'
const SEARCH_MAX_LEN = 10
const Token_KEY = '__token__'
const Name_KEY = '__name__'
const Pass_KEY = '__pass__'
const Page_KEY = '__page__'
const USER_ID = '__userid__'
const PIN_KEY = '__pin__'
const CERT_KEY = '__cert__'
const USER_URL = '__userurl__'
const XLH_KEY = '__xlh__'
const CA_LIST_KEY = 'CA_LIST_KEY' // 将半人我的项目历史
const PIN_LIST_KEY = 'PIN_LIST_KEY' // 代办事项历史
let hasKey = -1

// 插入函数 arr存储的数组 val传入储存的值 compare前后比较的函数 maxlen存入的最大长度
function insertArray(arr, val, compare, maxLen) {
  // 查找要传入的值是否已经在原存储的数据中存在，没有就返回-1
  const index = arr.findIndex(compare)
  if (index === 0) { // 如果传入值是原数组中的第一个元素，不做处理
    return
  }
  if (index > 0) { // 如果传入值在原数组中已经存在并且不是第一个，则删除原数组中的该元素并把这条数据插入到数组的第一位
    arr.splice(index, 1)
  }
  arr.unshift(val)
  if (maxLen && arr.length > maxLen) {
    // 如果有条数限制并且数组长度大于设置的最大长度，则将原数组的最后一个元素删除
    arr.pop()
  }
}

/**
 * 存储url地址
 */
export const setUrl = (key, value, userUrl = USER_URL) => {
  // urlAyy: [{key:'VUE_APP_BASE_API',value:''},{},{}]
  let urlArr = []
  let state = false
  if (storage.get(userUrl, [])) {
    urlArr = storage.get(userUrl, [])
  }
  if (urlArr.length === 0) {
    urlArr.push({ 'key': key, 'value': value })
    storage.set(userUrl, urlArr)
    const urlArr1 = storage.get(userUrl, [])
    console.log('保存url后------------------', urlArr1)
  } else {
    for (let i = 0; i < urlArr.length; i++) {
      if (key === 'VUE_APP_BASE_API') {
        if (key === urlArr[i].key) {
          urlArr[i].value = value
          storage.set(userUrl, urlArr)
          state = true
          continue
        }
      }
      if (key === 'VUE_APP_SC_BASE_API') {
        if (key === urlArr[i].key) {
          urlArr[i].value = value
          storage.set(userUrl, urlArr)
          state = true
          continue
        }
      }
      if (key === 'VUE_APP_API') {
        if (key === urlArr[i].key) {
          urlArr[i].value = value
          storage.set(userUrl, urlArr)
          state = true
          continue
        }
      }
      if (key === 'VUE_APP_FILE_API') {
        if (key === urlArr[i].key) {
          urlArr[i].value = value
          storage.set(userUrl, urlArr)
          state = true
          continue
        }
      }
      if (key === 'VUE_APP_CFCA_API') {
        if (key === urlArr[i].key) {
          urlArr[i].value = value
          storage.set(userUrl, urlArr)
          state = true
          continue
        }
      }
    }
    if (!state) {
      urlArr.push({ 'key': key, 'value': value })
      storage.set(userUrl, urlArr)
    }
  }
  const urlArr1 = storage.get(userUrl, [])
  console.log('保存url后------------------', urlArr1)
}

/**
 * 获取url
 */
export const getUrl = (key, urlName = USER_URL) => {
  // urlAyy: [{key:'VUE_APP_BASE_API',value:''},{},{}]
  const urlArr = storage.get(urlName)
  console.log('urlArr--------', urlArr)
  if (urlArr) {
    for (let i = 0; i < urlArr.length; i++) {
      if (urlArr[i].key === key) {
        return urlArr[i].value
      }
    }
  }
}

