// data.id, 0, data.query
export function saveSearch(key, query) {
  // const test = [['wdxm', [11, 2, 5, 9, 52, 1, 78]], ['dbsx', [3, 33, 56]]]
  // const test = [['dbsx', [3, 33, 56]]]
  // const test = [['wdxm', [11, 2, 5, 9, 52, 1, 78]], ['dbsx', [3, 33, 56]]]
  const userid = '001' // 以用户id作为唯一标识
  const searches = storage.get(userid, [])
  if (query != null && query.length > 0) {
    for (let i = 0; i < searches.length; i++) {
      if (searches[0] !== null) {
        if (searches[i][0] === key) {
          hasKey = i
        }
      }
    }
    if (hasKey === -1) {
      const queryarrays = []
      queryarrays.push(query)

      const arrays = []
      arrays.push(key)
      arrays.push(queryarrays)

      searches.push(arrays)
    } else {
      const arr = searches[hasKey][1]
      // 查找要传入的值是否已经在原存储的数据中存在，没有就返回-1
      var index = arr.findIndex((v) => {
        return v === query
      })
      if (index === 0) { // 如果传入值是原数组中的第一个元素，不做处理
        return
      }
      if (index > 0) { // 如果传入值在原数组中已经存在并且不是第一个，则删除原数组中的该元素并把这条数据插入到数组的第一位
        arr.splice(index, 1)
      }
      arr.unshift(query)
      if (SEARCH_MAX_LEN && arr.length > SEARCH_MAX_LEN) {
      // 如果有条数限制并且数组长度大于设置的最大长度，则将原数组的最后一个元素删除
        arr.pop()
      }
    }
    storage.set(userid, searches)
  }
  return searches
}
