import axios from '../index'

/**
 * 专家注册接口
 */
export const zjzc = ({ ztmc, ztdm, lxrdh, mm, email, fileList }) => {
  return axios.request({
    url: '/app/login/zj/saveJc_pszj',
    method: 'post',
    data: {
      ztmc,
      ztdm,
      lxrdh,
      mm,
      email,
      fileList
    }
  })
}
/**
 * 验证手机号接口
 */
export const checksjh = (sjh) => {
  return axios.request({
    url: '/app/login/zj/checkZjhm',
    method: 'post',
    data: {
      sjh
    }
  })
}
/**
* 根据二维码获取用户当前身份
*/
export const getSfxx = (token) => {
  return axios.request({
    url: 'app/qrcode/getParam',
    method: 'post',
    token: token
  })
}
/**
* 获取人脸识别状态
*/
export const getCertStatus = (data, baseurl, token) => {
  console.info('data===', data, 'baseurl=====', baseurl, 'token=====', token)
  return axios.request({
    baseURL: baseurl,
    url: '/app/ai/face/get_Cert_status',
    method: 'post',
    token: token,
    data
  })
}
