import axios from '../index'
import { getToken } from '../../common/js/catch'
/**
* 1.	获取供应商发货信息列表
*/
export const getXm_fhdhList = (data) => {
  return axios.request({
    url: '/app/ht/getXm_fhdhList',
    method: 'post',
    token: true,
    data
  })
}

