import '@/common/js/flex.js' // 移动端适配
import Vue from 'vue'
import router from './router'
// 引入Vuex的store对象
import store from './store'
import filters from './common/js/filters' // 过滤器
import App from './App'
// 引入fastclick并绑定到body 解决移动端点击响应延迟0.3s的问题
import FastClick from 'fastclick'
import '@/common/js/validate' // 校验文件
import axios from 'axios' // axios http请求库
// import VConsole from 'vconsole/dist/vconsole.min.js' // 全局插件
import Loading from '@/components/spin/index.js'
// import './base/spin/loading.vue'
import Vant from 'vant'
import { ImagePreview } from 'vant'
import 'vant/lib/index.css'
import 'vant/lib/icon/local.css'
import './assets/style/fonts/iconfont.css'
import './assets/style/animate.css'
import './assets/style/style.less' // 通用样式
import utils from './common/js/util' // 获取url参数
import Bridge from './common/js/bridge.js'
import apiModule from '@/api/apiModule.js'
import { Notify } from 'vant'
import AmapVue from '@amap/amap-vue'
// 引用高德地图组件 main.js
// import VueAMap from 'vue-amap'
// Vue.use(VueAMap)
// // 初始化
// VueAMap.initAMapApiLoader({
//   key: '481e918d0c70aa00924d71297ed30131',
//   plugin: ['AMap.PlaceSearch', 'AMap.Scale', 'Geocoder'], // 依赖配置，根据自己的需求引入
//   // 默认高德 sdk 版本为 1.4.4
//   v: '1.4.4'
// })
AmapVue.config.version = '2.0' // 默认2.0，这里可以不修改
AmapVue.config.key = '481e918d0c70aa00924d71297ed30131'
AmapVue.config.plugins = [
  'AMap.ToolBar',
  'AMap.MoveAnimation'
  // 在此配置你需要预加载的插件，如果不配置，在使用到的时候会自动异步加载
]
Vue.use(AmapVue)

Vue.prototype.$bridge = Bridge
Vue.use(Vant)
Vue.prototype.asyncApiModule = apiModule.asynApi
Vue.prototype.synApiModule = apiModule.synApi
Vue.prototype.$utils = utils
// var VueTouch = require('vue-touch')
// Vue.use(VueTouch, { name: 'v-touch' }) //右滑事件
// const vConsole = new VConsole() // 初始化
// require('./mock') //本地模拟数据
// axios.defaults.baseURL = 'http://mockjs.com/api' // 设置默认请求的url
Vue.prototype.$http = axios
Vue.prototype.$bus = new Vue()

Vue.use(Notify)
Vue.use(ImagePreview)
// Vue.use(vConsole)
// Vue.use(Navigation, { router, store })
Vue.config.productionTip = false // 将此值设置为 false ,会关闭 Vue 启动时的提示信息，推荐

/* 数据过滤器*/
for (const key in filters) {
  Vue.filter(key, (val, value1, value2) => {
    return filters[key](val, value1, value2)
  })
}

if ('addEventListener' in document) {
  document.addEventListener('DOMContentLoaded', function() {
    FastClick.attach(document.body)
  }, false)
}

/* eslint-disable no-new */
