import axios from '../index'
import { getToken } from '../../common/js/catch'
// const BASE_URL = '/api'

/**
 * 登录接口
 */
export const login = ({ username, password }) => {
  return axios.request({
    url: '/app/login/applogin',
    method: 'post',
    token: false,
    data: {
      username,
      password
    }
  })
}
export const gyxx = ({ zzid }) => {
  return axios.request({
    url: '/app/gys/gysxx?token=' + getToken(),
    method: 'post',
    token: false,
    data: {
      zzid
    }
  })
}
/**
 * 注册接口
 */
export const zc = ({ ztlb, zcd, ztmc, ztdm, lxr, lxrdh, mm, email }) => {
  return axios.request({
    url: '/app/login/jck/jc_ztbzt/saveJc_ztbzt',
    method: 'post',
    data: {
      ztlb,
      zcd,
      ztmc,
      ztdm,
      lxr,
      lxrdh,
      mm,
      email
    }
  })
}

/**
 * 登出接口
 */
export const logout = ({ username, password }) => {
  return axios.request({
    url: '/appsrv/ app/login/ applogout',
    method: 'post',
    token: false,
    data: {
      username,
      password
    }
  })
}

/**
 * 发送短信接口
 */
export const sendMessage = ({ sjh }) => {
  return axios.request({
    url: 'app/login/sms/sendMessage',
    method: 'post',
    token: false,
    data: {
      sjh
    }
  })
}

