// 获取首页推荐展示的热词
export function getSsHotwordsList(data) {
  return request({
    url: '/app/authentication/xtgl/hotwordsList',
    method: 'post',
    data
  })
}
// 新增首页搜索的热词
export function addSsHotwords(data) {
  return request({
    url: '/app/authentication/xtgl/savehotword',
    method: 'post',
    data
  })
}
// 获取首页订单数据
export function getOrderCount(data) {
  return request({
    url: '/app/authentication/order/getOrderCount',
    method: 'post',
    data
  })
}

