import { getSerachList } from '../../api/gys/serach'
import { loadSearch, saveSearch, clearSearch } from '../../common/js/catch'

// export const SET_SERACH_HISTOR = 'SET_SERACH_HISTOR' //搜索历史

// const Wybm_LS_KEY = '0' //我要报名历史
// const Wdxm_gys_LS_KEY = '1' //供应商我的项目历史

const state = {
  searchWybmList: loadSearch('Wybm_LS_KEY'), // 我要报名历史
  searchgysWdxmList: loadSearch('Wdxm_gys_LS_KEY') // 供应商我的项目历史
}

const getters = {
  searchWybmHistory: (state) => {
    return state.searchWybmList
  },
  searchgysWdxmHistory: (state) => {
    return state.searchgysWdxmList
  }

}
const mutations = {
  SET_WybmSERACH_HISTOR(state, params) {
    state.searchWybmList = params
  },
  SET_WdxmSERACH_HISTOR(state, params) {
    state.searchgysWdxmList = params
  }
}
const actions = {
  // eslint-disable-next-line no-unused-vars
  getWybmSerachList({ commit }, { pageno, zzid, xmmc }) {
    return new Promise((resolve, reject) => {
      getSerachList({ pageno, zzid, xmmc })
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit(SET_SERACH_HISTOR, res.data)
            resolve(res.data)
          } else {
            reject(res.text)
          }
        })
        .catch(error => {
          reject(error.toString())
        })
    })
  },
  // eslint-disable-next-line no-unused-vars
  getWdxmSerachList({ commit }, { pageno, zzid, xmmc }) {
    return new Promise((resolve, reject) => {
      getSerachList({ pageno, zzid, xmmc })
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit(SET_SERACH_HISTOR, res.data)
            resolve(res.data)
          } else {
            reject(res.text)
          }
        })
        .catch(error => {
          reject(error.toString())
        })
    })
  },

  // 清空搜索历史
  clearSearchHistory({ commit }, data) {
    if (data.key === '0') {
      commit('SET_WybmSERACH_HISTOR', clearSearch(getType(data.key)))
    } else {
      commit('SET_WdxmSERACH_HISTOR', clearSearch(getType(data.key)))
    }
  },
  // 更新搜索历史
  updateHostorList({ commit }, data) {
    if (data.key === '0') {
      commit('SET_WybmSERACH_HISTOR', saveSearch(getType(data.key), data.query))
    } else {
      commit('SET_WdxmSERACH_HISTOR', saveSearch(getType(data.key), data.query))
    }
  },
  // 更新搜索历史
  // eslint-disable-next-line no-unused-vars
  deleteSearchHistory({ commit }, data) {
    // console.info('query==5==', data)
    // console.info('query==5==', data.id)
    // console.info('query==5==', data.query)
    // commit(SET_SERACH_HISTOR, saveSearch(data.query))
  }

  // const Wybm_LS_KEY = '0' //我要报名历史
  // const Wdxm_gys_LS_KEY = '1' //供应商我的项目历史
  // const Wdxm_jbr_LS_KEY = '2' //将半人我的项目历史
  // const Dbsx_LS_KEY = '3' //代办事项历史

}
function getType(key) {
  switch (key) {
    case '0':
      return 'Wybm_LS_KEY'
    case '1':

      return 'Wdxm_gys_LS_KEY'
    case '2':

      return 'Wdxm_jbr_LS_KEY'
    case '3':

      return 'Dbsx_LS_KEY'
  }
}
export default {
  getters,
  state,
  mutations,
  actions
}
