import HttpRequest from '../common/js/axios1'
import { getToken, getUrl } from '@/common/js/catch'
const axios = new HttpRequest()
function getConfig(T) {
  let config = {}
  if (T.token) {
    config = {
      encryption: T.encryption,
      baseURL: T.baseUrl ? getUrl('VUE_APP_SC_BASE_API') : getUrl('VUE_APP_BASE_API'),
      headers: {
        'Content-Type': T.method === 'upload' ? 'multipart/form-data' : 'application/json; charset=UTF-8',
        'token': T.token !== true ? T.token : getToken()
      }
    }
  } else {
    config = {
      encryption: T.encryption,
      baseURL: T.baseUrl ? getUrl('VUE_APP_SC_BASE_API') : getUrl('VUE_APP_BASE_API'),
      headers: {
        'Content-Type': 'application/json; charset=UTF-8'
      }
    }
  }
  return config
}
function asynApi(T) {
  const config = getConfig(T)
  if (T.param) {
    return new Promise((resolve, reject) => {
      axios.request({
        config: config,
        url: T.url + '?' + T.param,
        method: T.method ? T.method : 'post',
        data: T.data,
        baseUrl: getUrl('VUE_APP_SC_BASE_API')
      }).then(res => {
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  } else {
    return new Promise((resolve, reject) => {
      axios.request({
        config: config,
        url: T.url,
        method: T.method ? T.method : 'post',
        data: T.data,
        baseUrl: getUrl('VUE_APP_SC_BASE_API')
      }).then(res => {
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  }
}
async function synApi(T) {
  const config = getConfig(T)
  if (T.param) {
    return await new Promise((resolve, reject) => {
      axios.request({
        config: config,
        url: T.url + '?' + T.param,
        method: T.method ? T.method : 'post',
        data: T.data,
        baseUrl: T.baseUrl
      }).then(res => {
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  } else {
    return await new Promise((resolve, reject) => {
      axios.request({
        config: config,
        url: T.url,
        method: T.method ? T.method : 'post',
        data: T.data,
        baseUrl: T.baseUrl
      }).then(res => {
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  }
}
export default{
  asynApi,
  synApi
}
