/*
 * 登录
 */
const mhlogin = r => require.ensure([], () => r(require('@/page/mh/home/login')), 'mhlogin')
const MhHome = r => require.ensure([], () => r(require('@/page/mh/home/MhHome')), 'MhHome') // 门户首页
const loading = r => require.ensure([], () => r(require('@/page/mh/home/loading')), 'loading') // 门户首页
const mhmore = r => require.ensure([], () => r(require('@/page/mh/home/mhmore')), 'mhmore') // 更多
const loginXy = r => require.ensure([], () => r(require('@/page/mh/home/loginXy')), 'loginXy') // 更多

const Dzxz = r => require.ensure([], () => r(require('@/page/erp/nq/Dzxz')), 'Dzxz') // 测试
export default [
  {
    path: '/',
    name: 'mhlogin',
    component: mhlogin
  },
  {
    path: '/Dzxz',
    name: 'Dzxz',
    component: Dzxz
  },
  {
    path: '/MhHome',
    name: 'MhHome',
    component: MhHome
  },
  {
    path: '/loading',
    name: 'loading',
    component: loading
  },
  {
    path: '/mhmore',
    name: 'mhmore',
    component: mhmore
  },
  {
    path: '/loginXy',
    name: 'loginXy',
    component: loginXy
  }
]
