import { login, logout, zc, gyxx } from '@/api/gys/user'
import { setToken, setUserName, setPass, setUserId } from '../../common/js/catch'
import { sendMessage } from '@/api/gys/user'
// const Base64 = require('js-base64').Base64
const state = {
  userInfo: '',
  userName: 'Zee',
  isLogin: false,
  rules: {}
}
const getters = {
  // firstLetter: (state) => {
  //   return state.userName.substr(0, 1)
  // }
}
const mutations = {
  SET_USER_INFO(state, params) {
    state.userInfo = params
  },
  SET_IS_LOGIN(state, params) {
    state.isLogin = params
  },
  SET_USER_NAME(state, params) {
    state.userName = params
  },
  SET_RULES(state, rules) {
    state.rules = rules
  }
}
const actions = {
  sendMessage({ commit }, { sjh }) {
    return new Promise((resolve, reject) => {
      sendMessage({ sjh }).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  },

  /* 注册*/
  zc({ commit }, { ztlb, zcd, ztmc, ztdm, lxr, lxrdh, mm, email }) {
    return new Promise((resolve, reject) => {
      zc({ ztlb, zcd, ztmc, ztdm, lxr, lxrdh, mm, email }).then(res => {
        if (res.state === 1) {
          setToken(res.data.token)
          commit('SET_USER_INFO', res.data)
          resolve(res)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  },

  /* 登录*/
  login({ commit }, { username, password }) {
    return new Promise((resolve, reject) => {
      login({ username, password }).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          setUserId(res.data.id)
          setToken(res.data.token) // 保存token
          setUserName(username) // 保存登录名称
          setPass(password) // 保存密码
          commit('SET_IS_LOGIN', true)
          commit('SET_USER_INFO', res.data)
          resolve(res.data)
        } else {
          reject(res.text)
        }
      }).catch(error => {
        reject(error)
      })
    })
  },

  /* 企业信息*/
  // eslint-disable-next-line no-unused-vars
  gyxx({ zzid }) {
    return new Promise((resolve, reject) => {
      gyxx({ zzid }).then(res => {
        if (res.state === 1) {
          resolve(res)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  },

  /* 登出*/
  logout({ commit }, { username, password }) {
    return new Promise((resolve, reject) => {
      logout({ username, password }).then(res => {
        if (res.state === 1) {
          setToken(res.data.token)
          commit('SET_USER_INFO', res.data)
          resolve(res)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  }
}

export default {
  getters,
  state,
  mutations,
  actions
}
