import { getWdxmList, getjbrXmjbxx, getjbrXmfbxx, getjbrXmgsxx } from '../../api/jbr/wdxm'
// import {setToken} from "../../common/js/catch"
// import { getWybmFbList } from '../../api/wybm'
// import {getWybmList} from "../../api/wybm";

const state = {
  // userInfo: ''
  allLists: [], // 全部
  wcLists: [], // 完成
  jxzLists: [], // 进行中
  searchWdxmList: [], // 搜索条件
  searchData: '', // 搜索条件
  wdxmItem: ''
}
const getters = {}
const mutations = {
  SET_WDXMITEM(state, params) {
    state.wdxmItem = params
  },
  SET_SEARCH_WDXM_LIST(state, params) {
    state.searchWdxmList = params
  },
  SET_JBEALL_LIST(state, params) {
    state.allLists = params
  },
  SET_BEWC_LIST(state, params) {
    state.wcLists = params
  },
  SET_BEJXZ_LIST(state, params) {
    state.jxzLists = params
  }

}

const actions = {
  /* 我的项目列表*/
  getjbrWdxmList({ commit }, T) {
    return new Promise((resolve, reject) => {
      getWdxmList(T).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)

        if (res.state === 1) {
          // setToken(res.data.token)
          // if (xmzt === 2) {
          //   commit('SET_BEWC_LIST', res.data)
          // } else if (xmzt === 1) {
          //   commit('SET_BEJXZ_LIST', res.data)
          // } else {
          // commit('SET_JBEALL_LIST', res.data)
          // }

          resolve(res.data)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  /* 项目基本信息*/
  getjbrXmjbxxAction({ commit }, id) {
    return new Promise((resolve, reject) => {
      getjbrXmjbxx(id).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          console.log(res.data)
          resolve(res.data)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  /* 项目分包信息*/
  getjbrXmfbxxAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      getjbrXmfbxx(T).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  },
  /* 项目概述信息*/
  getjbrXmgsxxAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      getjbrXmgsxx(T).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)
        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  }
}
export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions
}
