const dd = r => require.ensure([], () => r(require('@/page/dd/index')), 'dd') // 首页
const flash = r => require.ensure([], () => r(require('@/page/dd/flash/flash')), 'flash') // 闪屏

export default [
  {
    path: '/flash',
    name: 'flash',
    component: flash
  },
  {
    path: '/dd',
    name: 'dd',
    component: dd
  }
]
