import { Base64 } from 'js-base64'
/** 加密字符串 obj  true:json对象 其他 是字符串**/
export function encrypt(data, obj) {
  if (typeof data === 'undefined' || data === null || data === '' || JSON.stringify(data).toLowerCase() === 'null') {
    return null
  } else {
    return Base64.encode((obj === true || (obj && obj.toLowerCase() === 'true') ? JSON.stringify(data) : data), 'utf-8')
  }
}
/** 解密 **/
export function decrypt(data) {
  if (typeof data === 'undefined' || data === null || data === '' || JSON.stringify(data).toLowerCase() === 'null') {
    return null
  } else {
    try {
      // 判断字符是否是base64字符串
      if (btoa(atob(data))) {
        const result = Base64.decode(data)
        if (typeof JSON.parse(result) === 'object') {
          return JSON.parse(result)
        } else {
          return result
        }
      } else {
        return data
      }
    } catch (e) {
      return data
    }
  }
}
export function decrypt2(data) {
  if (typeof data === 'undefined' || data === null || data === '' || JSON.stringify(data).toLowerCase() === 'null') {
    return null
  } else {
    try {
      // 判断字符是否是base64字符串
      if (btoa(atob(data))) {
        const result = Base64.decode(data)
        return result
      } else {
        return data
      }
    } catch (e) {
      return data
    }
  }
}

