export const getTaskAction = ({ executorid }) => {
  return new Promise((resolve, reject) => {
    getTask(executorid)
      .then(response => {
        if (response.state === 1) {
          resolve(response.data)
        } else {
          reject(new Error('获取待办任务错误'))
        }
      })
      .catch(error => {
        reject(error)
      })
  })
}

