import axios from 'axios/index'
// import { baseURL } from './env'
import { removeToken } from './catch'
import { sm2 } from 'sm-crypto'
import Loading from '../../components/spin/index.js'
import '@/components/spin/loading'
import { getToken, getUrl } from '@/common/js/catch'
import { Toast } from 'vant'
// 引入uuid文件
import uuidv1 from 'uuid'
// import qs from 'qs'
const loading = Loading.get({})
class HttpRequest {
  // 设置baseUrl
  constructor(baseUrl = getUrl('VUE_APP_BASE_API')) {
    this.baseUrl = baseUrl // 根路径
    this.queue = {} // 请求队列
  }

  // 全局配置方法
  getInsideConfig(options) {
    console.info('options==' + JSON.stringify(options))

    let config = {}
    if (options.token !== false) {
      config = {
        baseURL: options.baseURL ? options.baseURL : getUrl('VUE_APP_BASE_API'),
        headers: {
          'Content-Type': options.method === 'upload' ? 'multipart/form-data' : 'application/json; charset=UTF-8',
          'token': options.token !== true ? options.token : getToken()
        }
      }
    } else {
      config = {
        baseURL: options.baseURL ? options.baseURL : getUrl('VUE_APP_BASE_API'),
        headers: {
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }
    }
    console.info('config.headers========' + JSON.stringify(config.headers))
    return config
  }

  distroy(url) {
    delete this.queue[url]
    if (!Object.keys(this.queue).length) {
      // Spin.hide()
      // this.$loading.hide()
      loading.hideLoading()
    }
  }

  // 请求拦截器
  interceptors(instance, url) {
    // 请求拦截器
    instance.interceptors.request.use(
      config => {
        // if (config.method === 'post') {
        //   config.data = qs.stringify(config.data)
        // }
        // 添加全局的loading...
        if (!Object.keys(this.queue).length) {
          // loading.isShowMask = false
          // loading.indicatorText = ''
          // loading.showNetTimeout()
          loading.showLoading()
        }
        // 队列处理

        // if (token) {
        //   console.log('getToken======', getToken())
        //   // config.headers['token'] = getToken()
        //   url = url + '?token=' + getToken()
        // }
        this.queue[url] = true
        // 每次发送请求之前判断是否存在token，如果存在，则统一在http请求的header都加上token，不用每次请求都手动添加了
        // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
        // const token = store.state.token;
        // token && (config.headers.Authorization = token);
        // const newData = { 'timestamp': getTimeStamp(), 'nonce': getNonce(), 'language': 'zh_cn' }
        // const params = Object.assign(config.data || {}, newData)
        if (config && config.data) {
          config.data = sm2.doEncrypt(JSON.stringify(config.data), '045ea58d916f18bdc9571986ee85fa7aa2c5d361b5fd7337bf74bcb6308468d35d709626f2a7b27ec271f3f4f215e074c8d7709950465aa23981749c3ea1381d41', 1)
        }
        return config
      },
      error => {
        // 请求失败
        return Promise.reject(error)
      }
    )
    // 响应拦截器
    instance.interceptors.response.use(
      // ==============  所有请求完成后都要执行的操作  ==================
      res => {
        this.distroy(url)
        // if (res && res.data) {
        //   res.data = sm2.doDecrypt(res.data, 'd8f59a77db2294826a0a54991e232c54be3686fbaa39620026f581c193957dbd', 1) // 解密结果
        // }
        if (res.data.state === 1) {
          return res.data
        } else {
          Toast(res.data.text)
          return Promise.reject(res.data.text)
        }
      },
      error => {
        // 响应失败
        loading.hideLoading()
        console.info('error-------', error)
        console.info('error.response-------', error.response)
        console.info('error.message-------', error.message)
        if (error.response == null || error.response === '') {
          console.info('error.message----aaa---', error.message)
          // this.$toast
          return Promise.reject('网络请求错误')
        }
        this.distroy('888888=', url)
        if (error.response.status) {
          // console.info('----11---', error)
          switch (error.response.status) {
            case 400:
              this.$toast.fail('请求错误(400)！')
              break
            case 401:
              this.$toast.fail('未授权，请重新登录')
              break
            case 403:
              removeToken()
              this.$toast.fail('登录过期，请重新登录')
              // store.commit('loginSuccess', null);
              // // 跳转登录页面，并将要浏览的页面fullPath传过去，登录成功后跳转需要访问的页面
              break
              // 404请求不存在
            case 404:
              this.$toast.fail('请求出错(404)！')
              break
            case 408:
              this.$toast.fail('请求超时(408)！')
              break
            case 500:

              this.$toast.fail('服务器错误(500)！')
              break
            case 501:
              this.$toast.fail('服务未实现(501)！')
              break
            case 502:
              this.$toast.fail('网络错误(502)！')
              break
            case 503:
              this.$toast.fail('服务不可用(503)！')
              break
            case 504:
              this.$toast.fail('网络超时(504)！')
              break
            case 505:
              this.$toast.fail('HTTP版本不受支持(505)！')
              break
              // 其他错误，直接抛出错误提示
            default:
          }
        } else {
          this.$toast.fail('连接超时')
        }
        return Promise.reject(error.response.data)
      }
    )
  }

  // 请求方法
  request(options) {
    const instance = axios.create({ timeout: 1000 * 12 })
    options = Object.assign(this.getInsideConfig(options), options) // 合并配置信息
    this.interceptors(instance, options.url, options.token) // 使用拦截器
    return instance(options)
  }
}
// 获取时间戳
const getTimeStamp = () => {
  return Date.parse(new Date().toString())
}
// 使用一次标记
const getNonce = () => {
  // 把所有字母和数字存到变量里
  var str = '0123456789qwertyuioplkjhgfdsazxcvbnmQWERTYUIOPLKJHGFDSAZXCVBNM'
  // 创建一个空字符串
  var code = ''
  // 循环4次, 每一次插入一个随机的英文字母或数字
  for (var i = 0; i < 4; i++) {
    var index = Math.floor(Math.random() * str.length)
    code += str[index]
  }
  return code
}
export default HttpRequest
