import request from '@/common/js/request'
export function getPermission(data) {
  return request({
    url: '/app/router/getVueRoleRouteJson',
    method: 'post',
    data,
    encryption: true
  })
}
export function getTokenById(data) {
  return request({
    url: '/app/login/applogin/getTokenById',
    method: 'post',
    data,
    encryption: true
  })
}
export function getModuls(data) {
  return request({
    url: '/app/login/getModuls',
    method: 'post',
    data: {
    },
    encryption: false
  })
}
export function login(data) {
  return request({
    url: '/app/login/applogin',
    method: 'post',
    data,
    encryption: true
  })
}
export function ananymous(data) {
  return request({
    url: '/app/login/ananymous',
    method: 'post',
    data,
    encryption: true
  })
}
export function getInfo(token) {
  return request({
    url: '/user/info',
    method: 'get',
    data: { token },
    encryption: true
  })
}

export function logout(data) {
  return request({
    url: 'app/authentication/applogout',
    method: 'post',
    data,
    encryption: false
  })
}
