import axios from '../index'

/**
 * 获取待办事项列表
 */
export const getxmsplist = ({ page, userid }) => {
  return axios.request({
    url: '/app/xmph/getsplslist',
    method: 'post',
    token: true,
    data: {
      page,
      userid
    }
  })
}
/**
 * 供应商：获取今日交易项目
 */
export const getSerachList = ({ pageno, zzid, xmmc }) => {
  return axios.request({
    url: '/app/xm/gys/kbmxmlist',
    method: 'post',
    token: true,
    data: {
      pageno,
      zzid,
      xmmc
    }
  })
}
