import axios from '../index'
import { getToken } from '../../common/js/catch'
// const BASE_URL = '/api'

/**
 * 获取首页菜单列表
 */
export const getVantCd = (data) => {
  return axios.request({
    url: '/app/router/getVantRoleRouteJson?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
/**
 * 获取首页底部菜单列表
 */
export const geBottomCd = (data) => {
  return axios.request({
    url: '/app/router/getAppBottomRoleRouteJson?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
/**
 * 登录接口
 */
export const login = (data) => {
  return axios.request({
    url: '/app/login/new/applogin',
    method: 'post',
    token: false,
    data
  })
}
/**
 * 登录接口
 */
export const codelogin = (data) => {
  return axios.request({
    url: '/app/login/codelogin',
    method: 'post',
    token: false,
    data
  })
}
/**
 * 获取用户详情接口
 */
export const getUserInfo = (data) => {
  return axios.request({
    url: '/app/zzqx/gg_ry/getGg_ryByRyid/' + data.id + '?token=' + getToken(),
    method: 'get',
    token: false
  })
}
/**
 * 获取桌面模块
 */
export const getZmMoudle = (data) => {
  return axios.request({
    url: '/app/zm/desktops/phone/' + data.id + '?token=' + getToken(),
    method: 'get',
    token: false
  })
}
/**
 * 获取桌面模块内容
 */
export const getMoudleHtml = (data) => {
  return axios.request({
    url: '/app/zm/desktops/getDestopHtml/' + data.moudleid + '/' + data.ryid + '/' + data.zzid + '?token=' + getToken(),
    method: 'get',
    token: false
  })
}
