import axios from '../index'
import { getToken } from '../../common/js/catch'
/**
* 获取待办事项列表
*/
export const getxmsplist = ({ page, userid }) => {
  return axios.request({
    url: '/app/xmph/getsplslist',
    method: 'post',
    token: true,
    data: {
      page,
      userid
    }
  })
}
/**
 * 供应商：获取今日交易项目
 */
export const getjrjyxmlist = (token, pageno, zzid, xmmc, jqdbsj) => {
  return axios.request({
    url: 'api/xm/gys/jqjyxmList?token=' + getToken(),
    method: 'post',
    token: token,
    data: {
      pageno,
      zzid,
      xmmc,
      jqdbsj
    }
  })
}
