import { getXtggList } from '../../api/jbr/xtgg'
// import {setToken} from "../../common/js/catch"
// import { getWybmFbList } from '../../api/wybm'
// import {getWybmList} from "../../api/wybm";

const state = {
  // userInfo: ''
  allLists: [], // 全部
  wcLists: [], // 完成
  jxzLists: [] // 进行中

}
const getters = {}
const mutations = {
  SET_JBEALL_LIST(state, params) {
    state.allLists = params
  },
  SET_BEWC_LIST(state, params) {
    state.wcLists = params
  },
  SET_BEJXZ_LIST(state, params) {
    state.jxzLists = params
  }
}

const actions = {
  /* 系统公告*/
  getXtggAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      getXtggList(T).then(response => {
        const respon = JSON.stringify(response)
        const res = JSON.parse(respon)

        if (res.state === 1) {
          resolve(res.data)
        } else {
          reject(new Error('错误'))
        }
      }).catch(error => {
        reject(error)
      })
    })
  }
}
export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions
}
