import axios from 'axios'
import store from '@/store'
import { getToken, getUrl } from '@/common/js/catch'
// import RSA from 'js-rsa-dave'
// import { getToken } from '@/utils/auth'
import router from '@/router/index'
import { encrypt } from '@/common/secure'

const service = axios.create({
  baseURL: getUrl('VUE_APP_SC_BASE_API'),
  // url = base url + request url
  // withCredentials: true, // send cookies when cross-domain requests
  timeout: 20000 // request timeout
})
// request interceptor
service.interceptors.request.use(
  async config => {
    // var baseURL = process.env.BASE_API

    // await axios.get('xymall/../../../serverConfig').then(e => {
    //   const config = e.data
    //   const ajaxUrl = config.production
    //   baseURL = ajaxUrl
    // })
    // config.baseURL = baseURL

    // config.baseURL = baseURL
    // init()
    if (config.url.toLowerCase().indexOf('/login/') === -1) {
      console.log('token--------', getToken())
      if (getToken()) {
        config.headers['token'] = getToken()
      }
      if (store.getters.token) {
        config.headers['token'] = store.getters.token
      } else {
        const token = JSON.parse(window.localStorage.getItem('token'))
        if (token) {
          config.headers['token'] = token
        }
      }
    }
    if (config.baseUrl) {
      config.url = config.baseUrl + config.url
    } else if (config.url.toLowerCase().indexOf('/app/') === 0) {
      config.url = getUrl('VUE_APP_API') + config.url
    }
    // config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
    // this.handleLogin()
    if (typeof (config.encryption) === 'undefined' || config.encryption) {
      if (config.data) {
        const newData = { 'timestamp': getTimeStamp(), 'nonce': getNonce(), 'language': 'zh_cn' }
        const params = Object.assign(config.data || {}, newData)
        // config.data = { 'params': RSA.encryptedString(RSA.getKeyPair(store.state.user.userInfo.public_exponent, '', store.state.user.userInfo.modulus), Base64.encode(JSON.stringify(config.data), 'utf-8')) }
        console.info('請求url====>', config.url)
        config.data = { 'params': encrypt(params, true) }
        // console.info('加密后参数====>', config.data)
      }
    }
    if (config.method === 'get') {
      config.params = {
        t: Date.parse(new Date()) / 1000,
        ...config.params
      }
    }
    return config
  },
  error => {
    // do something with request error
    console.log('错误=====', error) // for debug
    return Promise.reject(error)
  }
)
// 获取时间戳
const getTimeStamp = () => {
  return Date.parse(new Date().toString())
}
// 使用一次标记
const getNonce = () => {
  // 把所有字母和数字存到变量里
  var str = '0123456789qwertyuioplkjhgfdsazxcvbnmQWERTYUIOPLKJHGFDSAZXCVBNM'
  // 创建一个空字符串
  var code = ''
  // 循环4次, 每一次插入一个随机的英文字母或数字
  for (var i = 0; i < 4; i++) {
    var index = Math.floor(Math.random() * str.length)
    code += str[index]
  }
  return code
}
// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data
    let msg = '获取数据异常！'
    if (res !== 'token is not valid!') {
      if (res.text === '用户名不存在！' || res.text === '登录失败，密码错误！') {
        msg = '登录失败！'
      } else {
        msg = res.text
      }
      if (res.state !== 1) {
        Message({
          message: msg,
          type: 'error',
          duration: 5 * 1000
        })
        return Promise.reject('error')
      } else {
        return res
      }
    } else {
      const tokenout = JSON.parse(window.localStorage.getItem('tokenout'))
      console.info('tokenout====', tokenout)

      // if (tokenout === null || tokenout === '0') {
      router.replace({
        path: 'login',
        redirect: '/'
      })
      // } else if (tokenout === 1) {
      //   window.localStorage.setItem('tokenout', '1')
      //   MessageBox.confirm(
      //     '登录状态已过期，您可以继续留在该页面，或者重新登录',
      //     '系统提示',
      //     {
      //       confirmButtonText: '重新登录',
      //       cancelButtonText: '取消',
      //       type: 'warning'
      //     }
      //   ).then(() => {
      //     router.replace({
      //       path: 'login',
      //       redirect: '/login'
      //     })
      //   })
      // }
    }
  },
  error => {
    let code = 0
    try {
      code = error.response.data.status
      console.log(code, error.response.data.message)
    } catch (e) {
      if (error.toString().indexOf('Error: timeout') !== -1) {
        // Notification.error({
        //   title: '网络请求超时',
        //   duration: 2500
        // })
        return Promise.reject(error)
      }
      if (error.toString().indexOf('Error: Network Error') !== -1) {
        // Notification.error({
        //   title: '网络请求错误',
        //   duration: 2500
        // })
        return Promise.reject(error)
      }
    }
    if (code === 401) {
      MessageBox.confirm(
        '登录状态已过期，您可以继续留在该页面，或者重新登录',
        '系统提示',
        {
          confirmButtonText: '重新登录',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).then(() => {
        store.dispatch('user/logout').then(() => {
          location.reload() // 为了重新实例化vue-router对象 避免bug
        })
      })
    } else if (code === 403 || code === 404 || code === 500) {
      this.$router.push({
        path: '/404',
        params: { msg: code }
      })
    } else {
      try {
        console.info('error==1====', error)
        console.info('error==2====', error.response)
        const errorMsg = error.response.data.message
        // alert(errorMsg)
        if (errorMsg !== undefined) {
          // Notification.error({
          //   title: errorMsg,
          //   duration: 2500
          // })
        }
      } catch (e) {
        console.log(e)
      }
    }
    console.info('error==3====', error)
    console.info('error==4====', error.response)
    return Promise.reject(error)
  }
)

export default service
