/* 商城询价功能 */
const XjList = r => require.ensure([], () => r(require('@/page/mall/xj/XjList')), 'XjList') // 询价列表
const XjDetail = r => require.ensure([], () => r(require('@/page/mall/xj/XjDetail')), 'XjDetail') // 询价详情
const XjRule = r => require.ensure([], () => r(require('@/page/mall/xj/XjRule')), 'XjRule') // 发起询价
const XjResult = r => require.ensure([], () => r(require('@/page/mall/xj/XjResult')), 'XjResult') // 询价结果

/* 询价-个人中心功能 */
const XjMemberList = r => require.ensure([], () => r(require('@/page/mall/xj/member/XjMemberList')), 'XjMemberList') // 询价列表
const XjMemberEdit = r => require.ensure([], () => r(require('@/page/mall/xj/member/XjMemberEdit')), 'XjMemberEdit') // 询价-编辑信息
const XjMemberView = r => require.ensure([], () => r(require('@/page/mall/xj/member/XjMemberView')), 'XjMemberView') // 询价-查看信息
const XjMemberHistory = r => require.ensure([], () => r(require('@/page/mall/xj/member/XjMemberHistory')), 'XjMemberHistory') // 询价历史
const XjMemberResult = r => require.ensure([], () => r(require('@/page/mall/xj/member/XjMemberResult')), 'XjMemberResult') // 询价-入围结果

export default [
  { path: '/XjList', name: 'XjList', component: XjList },
  { path: '/XjDetail', name: 'XjDetail', component: XjDetail },
  { path: '/XjResult', name: 'XjResult', component: XjResult },
  { path: '/XjRule', name: 'XjRule', component: XjRule },
  { path: '/XjMemberList', name: 'XjMemberList', component: XjMemberList },
  { path: '/XjMemberEdit', name: 'XjMemberEdit', component: XjMemberEdit },
  { path: '/XjMemberView', name: 'XjMemberView', component: XjMemberView },
  { path: '/XjMemberHistory', name: 'XjMemberHistory', component: XjMemberHistory },
  { path: '/XjMemberResult', name: 'XjMemberResult', component: XjMemberResult }
]
