/* 商城竞价功能 */
const BidList = r => require.ensure([], () => r(require('@/page/mall/bid/BidList')), 'BidList') // 竞价列表
const BidDetail = r => require.ensure([], () => r(require('@/page/mall/bid/BidDetail')), 'BidDetail') // 竞价详情
const BidRule = r => require.ensure([], () => r(require('@/page/mall/bid/BidRule')), 'BidRule') // 竞价-设置竞价规则
const BidResult = r => require.ensure([], () => r(require('@/page/mall/bid/BidResult')), 'BidResult') // 竞价结果详情

/* 竞价-个人中心功能 */
const BidMemberList = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberList')), 'BidMemberList') // 竞价列表
const BidMemberEdit = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberEdit')), 'BidMemberEdit') // 竞价-编辑信息
const BidMemberView = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberView')), 'BidMemberView') // 竞价-查看信息
const BidMemberHistory = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberHistory')), 'BidMemberHistory') // 竞价-竞价历史
const BidMemberResult = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberResult')), 'BidMemberResult') // 竞价-入围结果

export default [
  { path: '/BidList', name: 'BidList', component: BidList },
  { path: '/BidDetail', name: 'BidDetail', component: BidDetail },
  { path: '/BidRule', name: 'BidRule', component: BidRule },
  { path: '/BidResult', name: 'BidResult', component: BidResult },
  { path: '/BidMemberList', name: 'BidMemberList', component: BidMemberList },
  { path: '/BidMemberEdit', name: 'BidMemberEdit', component: BidMemberEdit },
  { path: '/BidMemberView', name: 'BidMemberView', component: BidMemberView },
  { path: '/BidMemberHistory', name: 'BidMemberHistory', component: BidMemberHistory },
  { path: '/BidMemberResult', name: 'BidMemberResult', component: BidMemberResult }
]
