import { getNoticesList } from '@/api/sc/notices'

import { getProImgList, getSyHotwordsList, getSsHotwordsList, addSsHotwords, getOrderCount, getSceneList, getScenePointList, getSyZqList } from '@/api/sc/home'
import { getSyHotProduct, getSyDszqhotProduct, getSyListCart } from '@/api/sc/goods'
const state = {
  dsbs: '', // 用户信息
  ids: '', // 分类信息
  categoryId: '', // 用户信息
  searchInput: '', // 用户信息
  count: 0,
  xmxh: ''
}

const mutations = {
  SET_XMXH: (state, params) => {
    state.xmxh = params
  },
  SET_COUNT: (state, params) => {
    state.count = params
  },
  SET_DSBS: (state, params) => {
    state.dsbs = params
  },
  SET_IDS: (state, params) => {
    state.ids = params
  },
  SET_Category_Id: (state, params) => {
    state.categoryId = params
  },
  SET_Search_Input: (state, params) => {
    state.searchInput = params
  }
}

const actions = {
  setdsbs({ commit }, data) {
    commit('SET_DSBS', data)
  },
  setids({ commit }, data) {
    commit('SET_IDS', data)
  },
  setCategoryId({ commit }, data) {
    commit('SET_Category_Id', data)
  },
  setSearchInput({ commit }, data) {
    commit('SET_Search_Input', data)
  },
  // user login
  getNotices({ commit }, T) {
    // const { pageNo, pageSize } = userInfo
    return new Promise((resolve, reject) => {
      getNoticesList(T).then(response => {
        // const { data } = response
        console.log('返回数据=====', response)
        // commit(SET_TOKEN, data.token)
        // setToken(data.token)
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 获取首页banner图
  getProImgListAction({ commit }) {
    return new Promise((resolve, reject) => {
      getProImgList().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  // 获取首页电商商品
  getSyDszqhotProductAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      getSyDszqhotProduct(T).then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 获取首页热销商品
  getSyHotProductAction({ commit }) {
    return new Promise((resolve, reject) => {
      getSyHotProduct().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  getSyHotwordsListAction({ commit }) {
    // const { pageNo, pageSize } = userInfo
    return new Promise((resolve, reject) => {
      getSyHotwordsList().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 获取首页场景列表
  getSceneListAction({ commit }) {
    return new Promise((resolve, reject) => {
      getSceneList().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // 获取场景点列表
  getScenePointListAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      getScenePointList(T).then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  getSsHotwordsListAction({ commit }) {
    // const { pageNo, pageSize } = userInfo
    return new Promise((resolve, reject) => {
      getSsHotwordsList().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  getOrderCountAction({ commit }) {
    return new Promise((resolve, reject) => {
      getOrderCount().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  addSsHotwordsAction({ commit }, T) {
    // const { pageNo, pageSize } = userInfo
    return new Promise((resolve, reject) => {
      addSsHotwords(T).then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  getSyZqListAction({ commit }) {
    return new Promise((resolve, reject) => {
      getSyZqList().then(response => {
        resolve(response.data)
      }).catch(error => {
        reject(error)
      })
    })
  }

}
// get user info

const getters = {

}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}

