import axios from '../index'
import { getToken } from '../../common/js/catch'
/**
 * 系统公告
 */
export const getXtggList = (data) => {
  return axios.request({
    url: 'api/xtgg/getXtggList?token=' + getToken(),
    method: 'post',
    token: true,
    data
  })
}
