import { login, logout, getInfo, ananymous, getPermission, getTokenById } from '@/api/sc/user'
import { resetRouter } from '@/router'
import { SET_TOKEN, GET_TOKEN, ADD_USERINFO, GET_USERINFO, SET_PUBLICKEY } from '@/store/sc/constants-store'
import { setToken } from '../../common/js/catch'
const state = {
  token: '',
  userInfo: {}, // 用户信息,
  dlh: '' // 登录号

}

const mutations = {
  SET_TOKEN(state, token) {
    state.token = token
  },
  SET_DLH(state, token) {
    state.dlh = token
  },
  SET_USER_INFO(state, token) {
    state.userInfo = token
  },
  // [SET_TOKEN]: (state, token) => {
  //   state.token = token
  // },

  [GET_TOKEN]: (state, token) => {
    return state.token
  },
  [ADD_USERINFO]: (state, userInfo) => {
    state.userInfo = userInfo
  },
  // [DELLTE_USERINFO]: (state) => {
  //   state.userInfo = {}
  // },
  [GET_USERINFO]: (state) => {
    return state.userInfo
  },
  [SET_PUBLICKEY]: (state, userInfo) => {
    state.userInfo.modulus = userInfo.modulus
    state.userInfo.public_exponent = userInfo.public_exponent
    return state.userInfo
  }
}

const actions = {
  setToken({ commit }, data) {
    commit('SET_TOKEN', data)
  },

  // get user info
  getVueRoleRouteJson({ commit, state }, T) {
    return new Promise((resolve, reject) => {
      getPermission(T).then(response => {
        const { data } = response
        // if (!data) {
        //   reject('Verification failed, please Login again.')
        // }
        resolve(data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  // 获取公钥
  // getModulsAction({ commit }) {
  //   return new Promise((resolve, reject) => {
  //     getModuls().then(response => {
  //       const { data } = response
  //       commit(SET_PUBLICKEY, data)
  //       resolve()
  //     }).catch(error => {
  //       reject(error)
  //     })
  //   })
  // },
  // user login
  login({ commit }, userInfo) {
    console.log('userInfo-----------------------', userInfo)
    return new Promise((resolve, reject) => {
      login({ username: userInfo.userAccount, password: userInfo.passWord }).then(response => {
        const { data } = response
        console.log('data----------------------------1', data)
        // commit('SET_DLH', userInfo.userAccount.trim())
        commit('SET_TOKEN', data.token)
        commit('SET_USER_INFO', data)
        commit(ADD_USERINFO, data)
        setToken(data.token)
        // window.localStorage.setItem('token', JSON.stringify(data.token))

        // setToken(data.token)
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },

  getTokenByIdAction({ commit }, userInfo) {
    return new Promise((resolve, reject) => {
      getTokenById(userInfo).then(response => {
        const { data } = response
        commit(SET_TOKEN, data.token)
        commit(ADD_USERINFO, data)
        // setToken(data.token)
        resolve(data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  // get user info
  getAnanymous({ commit }) {
    return new Promise((resolve, reject) => {
      ananymous().then(response => {
        const { data } = response
        if (!data) {
          reject('Verification failed, please Login again.')
        }
        commit(SET_TOKEN, response.data.token)
        commit(ADD_USERINFO, data)
        // window.localStorage.setItem('token', JSON.stringify(data.token))

        // const token = JSON.parse(window.localStorage.getItem('token'))
        // console.info('token======', token)
        // setToken(data.token)
        resolve(data)
      }).catch(error => {
        reject(error)
      })
    })
  },
  // get user info
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo(state.token).then(response => {
        const { data } = response
        if (!data) {
          reject('Verification failed, please Login again.')
        }
        const { userInfo } = data
        console.log(userInfo, data)
        commit(ADD_USERINFO, { userInfo })
        resolve(data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  // user logout
  logout({ commit, state }) {
    return new Promise((resolve, reject) => {
      logout().then((res) => {
        console.info('res========', res)
        commit(SET_TOKEN, '')
        commit(ADD_USERINFO, '')
        // removeToken()
        resetRouter()
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },

  // 页面跳转
  turnToPage({ commit }, userInfo) {
    return new Promise(resolve => {
      commit(SET_TOKEN, userInfo.token)
      commit(ADD_USERINFO, userInfo)
      // setToken(userInfo.token)
      resolve(userInfo)
    })
  }
}

