import Vue from 'vue'
import Vuex from 'vuex'
// import state from './state'
// import getters from './getters'
// import mutations from './mutations'
// import actions from './actions'
import mh from './mh/mh'
import gysuser from './gys/user'
import gyshome from './gys/home'
import gyswybm from './gys/wybm'
import gyswdxm from './gys/wdxm'
import jbruser from './jbr/user'
import jbrdbsx from './jbr/dbsx'
import jbrwdxm from './jbr/wdxm'
import gyssearch from './gys/search'
import jbrsearch from './jbr/search'
import jbrxtgg from './jbr/xtgg'
import mall from './mall/mall'
import ui from './gys/ui'
import fhgl from './gys/fhgl'
// import dd from './dd/dd'
import app from './sc/app'
import bid from './sc/bid'
import goods from './sc/goods'
import home from './sc/home'
import member from './sc/member'
import settings from './sc/settings'
import user from './sc/user'
import ca from './ca/ca'
// import mixin from './plugin/mixin'
import saveInLocal from './plugin/saveInLocal'
import router from './gg/router'
import zj from './zj/zj'
import erp from './erp/erp'
// import router from './module/router'

Vue.use(Vuex)

const store = new Vuex.Store({
  // strict: false,
  // state,
  // getters,
  // mutations,
  // actions,
  modules: {
    app,
    bid,
    goods,
    home,
    member,
    settings,
    user,
    mh,
    gysuser,
    gyshome,
    gyswybm,
    gyswdxm,
    gyssearch,
    jbrsearch,
    ui,
    jbruser,
    jbrdbsx,
    router,
    jbrwdxm,
    jbrxtgg,
    zj,
    ca,
    mall,
    fhgl,
    erp
    //   router,
  },
  plugins: [saveInLocal]
})
export default store
