/**
 * 图片缩放
 */
export function getDeviceRatio() {
  // var isAndroid = window.navigator.appVersion.match(/android/gi)
  var isIPhone = window.navigator.appVersion.match(/iphone/gi)
  var devicePixelRatio = window.devicePixelRatio
  var dpr
  if (isIPhone) {
    // iOS下，对于2和3的屏，用2倍的方案，其余的用1倍方案
    if (devicePixelRatio >= 3) {
      dpr = 3
    } else if (devicePixelRatio >= 2) {
      dpr = 2
    } else {
      dpr = 1
    }
  } else {
    // 其他设备下，仍旧使用1倍的方案
    dpr = 1
  }
  return dpr
}
// 时间字符串格式化
export function formatTimes(value) {
  value = value + ''
  if (value != null && value !== '') {
    if (value.length === 14) {
      return insertStr(insertStr(insertStr(insertStr(insertStr(value, 4, '/'), 7, '/'), 10, '  '), 14, ':'), 17, ':')
    }
    if (value.length === 12) {
      return insertStr(insertStr(insertStr(insertStr(value, 4, '/'), 7, '/'), 10, '  '), 14, ':')
    }
    if (value.length === 8) {
      return insertStr(insertStr(value, 4, '/'), 7, '/')
    }
  } else {
    return ''
  }
}
function insertStr(soure, start, newStr) {
  return soure.slice(0, start) + newStr + soure.slice(start)
}
export function debounce(func, delay) {
  let timer
  return function(...args) {
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(() => {
      func.apply(this, args)
    }, delay)
  }
}
export default{
  getUrlKey: function(name) {
    // eslint-disable-next-line no-sparse-arrays
    return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ''])[1].replace(/\+/g, '%20')) || null
  }
}
