// import App from '../App'

import Vue from 'vue'
import VueRouter from 'vue-router'
import { setPage } from '../common/js/catch' // 状态数据
import gysRouters from './gys/routers' // 主页
import jbrRouters from './jbr/routers' // 经办人
import gg from './gg/gg' // 公共页面路径
import dd from './dd/home' // 钉钉主页
import mallRouters from './mall/routers' // 商城
import mhRouters from './mh/home' // 门户首页

import bid from './bid/routers' // 竞价
import xj from './xj/routers' // 询价
import bidhall from './bidhall/routers' // 竞价大厅
import zjRouters from './zj/routers' //  专家首页
import erp from './erp/routers' // 信源ERP

Vue.use(VueRouter)
const RouterModel = new VueRouter({

  // mode: 'history',
  // base: '/gqcg/',

  routes: [
    ...xj,
    ...gysRouters,
    ...jbrRouters,
    ...gg,
    ...dd,
    ...mallRouters,
    ...mhRouters,
    ...bid,
    ...bidhall,
    ...zjRouters,
    ...erp
  ],
  // 解决vue页面之间跳转，页面不是在顶部的问题
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})
// 书写全局拦截守卫，router是声明的路由组件
RouterModel.beforeEach((to, from, next) => {
  /* 显示加载中动画 */
  // this.$store.commit('updateLoadingStatus', true)
  if (to.matched.some(record => record.meta.lines)) { // 第一个检测跳转的页面是否设置了未登录不可查看权限
    next()
    setPage(true)
    // this.$store.commit('SET_PAGELINES', false)
  } else {
    next() // 确保一定要调用 next()
    setPage(false)
    // this.$store.commit('SET_PAGELINES', true)
  }
})
RouterModel.afterEach(route => {
  /* 隐藏加载中动画 */
  // this.$store.commit('updateLoadingStatus', false)
})

export default RouterModel
