exports.formatMoney = (value) => {
  if (value) {
    value = Number(value)
    return '￥ ' + value.toFixed(2)
  }
}
/*
处理倒计时
 */
exports.formatDayTime = (value) => {
  if (value) {
    if (value.length === 6) {
      return value.substring(0, 2) + '时' +
        value.substring(2, 4) + '分' +
        value.substring(4, 6) + '秒'
    } else {
      return value.substring(0, value.length - 6) + '天' +
             value.substring(value.length - 6, value.length - 4) + '时' +
             value.substring(value.length - 4, value.length - 2) + '分' +
             value.substring(value.length - 2, value.length) + '秒'
    }
  }
}
