exports.statusName = (val) => {
  let statusName = ''
  switch (val) {
    case 0: {
      statusName = '已取消'
      break
    }
    case 10: {
      statusName = '未付款'
      break
    }
    case 20: {
      statusName = '已付款'
      break
    }
  }
  return statusName
}
// 时间字符串格式化
exports.cheakTime = () => {
  var begin = new Date('2018-10-8 10:00:10')
  var end = new Date('2018-10-8 20:05:00')
  // 时间相差毫秒数
  var span = end.getTime() - begin.getTime()

  // 计算相差天数
  var result = ''
  var days = Math.floor(span / (24 * 3600 * 1000))
  result += days + '天'
  // 相差小时数
  var leave1 = span % (24 * 3600 * 1000)
  var hours = Math.floor(leave1 / (3600 * 1000))
  result += hours + '小时'
  // 相差分钟
  var leave2 = leave1 % (3600 * 1000)
  var minutes = Math.floor(leave2 / (60 * 1000))
  result += minutes + '分钟'
  // 相差秒
  var level3 = leave2 % (60 * 1000)
  var seconds = Math.round(level3 / 1000)
  result += seconds + '秒'

  return result
}
// 时间字符串格式化
exports.formatTime = (value) => {
  if (value != null && value !== '') {
    if (value.length === 14) {
      return insertStr(insertStr(insertStr(insertStr(insertStr(value, 4, '/'), 7, '/'), 10, '  '), 14, ':'), 17, ':')
    }
    if (value.length === 12) {
      return insertStr(insertStr(insertStr(insertStr(value, 4, '/'), 7, '/'), 10, '  '), 14, ':')
    }
  } else {
    return ''
  }
}
// 时间字符串格式化
exports.formatTime12 = (value) => {
  if (value != null && value !== '') {
    const values = value.toString()
    return insertStr(insertStr(insertStr(insertStr(values, 4, '/'), 7, '/'), 10, '  '), 14, ':')
  } else {
    return ''
  }
}
// 时间字符串格式化
exports.formatTime14 = (value) => {
  if (value != null && value !== '') {
    const values = value.toString()
    if (values.length === 14) {
      return insertStr(insertStr(insertStr(insertStr(insertStr(values, 4, '/'), 7, '/'), 10, '  '), 14, ':'), 17, ':')
    }
    if (values.length === 12) {
      return insertStr(insertStr(insertStr(insertStr(values, 4, '/'), 7, '/'), 10, '  '), 14, ':')
    }
  } else {
    return ''
  }
}
function insertStr(soure, start, newStr) {
  return soure.slice(0, start) + newStr + soure.slice(start)
}
