const BidHallList = r => require.ensure([], () => r(require('@/page/bidhall/BidHallList')), 'BidHallList') // 竞价大厅列表
const BidHallListNoImg = r => require.ensure([], () => r(require('@/page/bidhall/BidHallListNoImg')), 'BidHallListNoImg') // 竞价大厅列表-无图版
const BidGysHall = r => require.ensure([], () => r(require('@/page/bidhall/gys/BidGysHall')), 'BidGysHall') // 供应商竞价大厅-有图版
const BidJbrHall = r => require.ensure([], () => r(require('@/page/bidhall/jbr/BidJbrHall')), 'BidJbrHall') // 经办人竞价大厅
const BidMsgList = r => require.ensure([], () => r(require('@/page/bidhall/BidMsgList')), 'BidMsgList') // 消息列表
const BidSupList = r => require.ensure([], () => r(require('@/page/bidhall/BidSupList')), 'BidSupList') // 选择供应商列表

export default [
  {
    path: '/BidHallList',
    name: 'BidHallList',
    component: BidHallList
  },
  {
    path: '/BidHallListNoImg',
    name: 'BidHallListNoImg',
    component: BidHallListNoImg
  },
  {
    path: '/BidGysHall',
    name: 'BidGysHall',
    component: BidGysHall
  },
  {
    path: '/BidJbrHall',
    name: 'BidJbrHall',
    component: BidJbrHall
  },
  {
    path: '/BidMsgList',
    name: 'BidMsgList',
    component: BidMsgList
  },
  {
    path: '/BidSupList',
    name: 'BidSupList',
    component: BidSupList
  }
]
