import {
  caLogin,
  CALoad,
  caSmqz,
  caSmJm,
  caSmJJm } from '../../api/ca/ca'
const state = {
  caListItem: '',
  caNUM: 0, // ca序列号
  caPIN: ''
}
const getters = {
  getPageList: (state) => {
    return state.pageList
  }
}
const mutations = {
  SET_CaListItem(state, params) {
    state.caListItem = params
  },
  SET_CaPin(state, params) {
    state.caPIN = params
  },
  SET_CaNUM(state, params) {
    state.caNUM = params
  }

}
const actions = {
  setCaListItem({ commit }, data) {
    commit('SET_CaListItem', data)
  },
  setCaPin({ commit }, data) {
    commit('SET_CaPin', data)
  },
  setCaNUM({ commit }, data) {
    commit('SET_CaNUM', data)
  },

  CaLoginAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      caLogin(T)
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit('SET_USER_INFO', res.data)
            resolve(res.data)
          } else {
            reject(new Error('错误'))
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  LoadCA({ commit }, code, T) {
    return new Promise((resolve, reject) => {
      CALoad(code, T)
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit('SET_USER_INFO', res.data)
            resolve(res.data)
          } else {
            reject(new Error('错误'))
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  /**
*  移动证书签章接口
*/
  caSmqzAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      caSmqz(T.value, T.token)
        .then(res => {
          if (res.state === 1) {
            // setToken(res.data.token)
            // commit('SET_USER_INFO', res.data)
            resolve(res.data)
          } else {
            reject(new Error('错误'))
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  /**
 * 移动证书扫码解密
 */
  caSmJmAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      caSmJJm(T.value, T.token)
        .then(res => {
          if (res.state === 1) {
            resolve(res.data)
          } else {
            reject(new Error('错误'))
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  },
  /**
 * 移动证书扫码加密
 */
  caSmJJmAction({ commit }, T) {
    return new Promise((resolve, reject) => {
      caSmJJm(T.value, T.token)
        .then(res => {
          if (res.state === 1) {
            resolve(res.data)
          } else {
            reject(new Error('错误'))
          }
        })
        .catch(error => {
          reject(error)
        })
    })
  }
}
export default {
  namespaced: true,
  getters,
  state,
  mutations,
  actions
}
